.container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 140px;
  margin-left: 10%;
  margin-right: auto;
  gap: 20px;
  grid-template-columns: repeat(2, 1fr);
  width: 70vh;
}
.main_div {
  background-repeat: no-repeat;
  background-size: auto;
  display: grid;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  margin-right: 10px;
}
